/**
 * Agency Handoff Documents API
 * AUDIT TASK #5: Secure document access for agency handoff cases
 *
 * GET /api/agency/handoffs/[handoffId]/documents - List all documents
 * GET /api/agency/handoffs/[handoffId]/documents?signed=true - Get signed URLs
 *
 * Authorization:
 * - Freelancer who created the handoff
 * - Agency assigned to the handoff
 * - Admin users
 */

import { NextRequest, NextResponse } from 'next/server';
import { auth } from '@clerk/nextjs/server';
import { db } from '@/lib/firebase';
import { listHandoffDocuments, getSignedUrl } from '@/lib/firebase-storage';
import { logInfo, logError } from '@/utils/logger';
import { errors, handleApiError } from '@/utils/error';

export const dynamic = 'force-dynamic';

/**
 * GET - List documents or get signed URLs
 */
export async function GET(
  req: NextRequest,
  { params }: { params: Promise<{ handoffId: string }> }
): Promise<NextResponse> {
  try {
    // 1. Authenticate
    const { userId } = await auth();
    if (!userId) {
      throw errors.unauthorized();
    }

    const { handoffId } = await params;

    // 2. Get handoff to verify authorization
    const handoffDoc = await db.collection('agency_handoffs').doc(handoffId).get();

    if (!handoffDoc.exists) {
      throw errors.notFound('Handoff not found');
    }

    const handoff = handoffDoc.data();

    // 3. Check authorization
    // User must be the freelancer who created the handoff or an admin
    const userDoc = await db.collection('users').doc(userId).get();
    const user = userDoc.data();

    const isFreelancer = handoff?.freelancerId === userId;
    const isAdmin = user?.role === 'admin';

    if (!isFreelancer && !isAdmin) {
      throw errors.forbidden('Not authorized to access these documents');
    }

    // 4. List documents
    const result = await listHandoffDocuments(handoffId, handoff?.freelancerId);

    if (!result.success) {
      throw new Error(result.error || 'Failed to list documents');
    }

    // 5. Check if signed URLs requested
    const searchParams = req.nextUrl.searchParams;
    const wantSignedUrls = searchParams.get('signed') === 'true';

    if (wantSignedUrls && result.documents) {
      // Generate signed URLs for all documents
      const documentsWithUrls = await Promise.all(
        result.documents.map(async (doc) => {
          const signedUrlResult = await getSignedUrl(doc.storagePath, 60); // 1 hour expiry

          return {
            ...doc,
            signedUrl: signedUrlResult.success ? signedUrlResult.signedUrl : null,
            signedUrlExpiresAt: signedUrlResult.success ? signedUrlResult.expiresAt : null,
          };
        })
      );

      logInfo('Generated signed URLs for handoff documents', {
        handoffId,
        documentCount: documentsWithUrls.length,
      });

      return NextResponse.json({
        handoffId,
        documents: documentsWithUrls,
      });
    }

    // 6. Return document list without signed URLs
    return NextResponse.json({
      handoffId,
      documents: result.documents,
    });

  } catch (error: any) {
    logError('Failed to get handoff documents', error);
    return handleApiError(error);
  }
}
